import { Component, OnInit } from '@angular/core';
import { UsuarioInfoService } from '../../services/usuario-info.service';
import { Usuario } from '../../models/usuario';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import {Router} from "@angular/router";

@Component({
  selector: 'sa-usuario',
  templateUrl: './usuario.component.html',
  styleUrls: ['./usuario.component.css']
})

export class UsuarioComponent implements OnInit {
  usuariodateList : Usuario[];
  display: boolean = false;
  display2: boolean = false;
  display3: boolean = false;
  selectedUser: Usuario;
  users: Usuario[];
  usuario_user: Object[] = [];
  usuario_edit: Object[] = [];
  user: Usuario = {};
  cols: any[];
  newUsuario: boolean;
  editUsuario: boolean;
  usuario: Usuario;
  usuarios: Array<any>;

  adunit: any = {};


  constructor(private usuarioService: UsuarioInfoService, private formBuilder: FormBuilder,private router: Router) { }
  formulario: FormGroup;
  formularioEdit: FormGroup;
  ngOnInit() {
    this.getAllUser();
    this.configurarFormulario();
    this.configurarFormularioEdit();
    this.cols = [
            { field: 'id', header: 'ID' },
            { field: 'name', header: 'Nome' },
            { field: 'email', header: 'E-Mail' },
            { field: 'created_at', header: 'Data de Cria칞칚o' },
            { field: 'Editar', header: 'Editar' },
            { field: 'Deletar', header: 'Deletar' }
    ];

  }

  showDialogToAdd() {
    this.display = true;
    this.newUsuario = true;
    this.user = {};
   }

  getAllUser(){
    this.usuarioService.getAllUser().subscribe(
      data => {
          this.usuariodateList = data;
          console.log(this.usuariodateList);
      }
    ); 
  }

  selectUsuario(usuario: Usuario){
    this.usuario = usuario;
  }

  configurarFormulario(){
      this.formulario = this.formBuilder.group({
          name: [null, Validators.required],
          email: [null,Validators.required],
          password: [null,Validators.required]
      });
  }

  configurarFormularioEdit(){
    this.formularioEdit = this.formBuilder.group({
      id:[''],
      name: [null, Validators.required],
      email: [null, Validators.required],
      password: [null]
    });
  }

  criar() {
    this.usuarioService.criar(this.formulario.value).subscribe( data => {
      this.router.navigate(['usuario']);
      this.formulario.reset();
      window.location.reload();
    });
  }

  update(user: Usuario){
    this.display2 = true;
    this.usuarioService.getUserById(+user.id)
    .subscribe( data => {
      this.formularioEdit.patchValue(data);
    });
  }


  deleteModal(){
    this.display3 = true;
  }

  delete(user: Usuario) {
    this.usuarioService.delete(user.id)
      .subscribe( data => {
        this.usuario_user = this.usuario_user.filter(u => u !== user);
      });
      let timeout = 1000;
      setTimeout(() => {
        window.location.reload();
      }, timeout);
  };

  onRowSelect(event) {
    this.newUsuario = false;
    this.user = this.cloneUser(event.data);
    this.display = true;
  }

  cloneUser(c: Usuario): Usuario {
    let user = {};
    for (let prop in c) {
      user[prop] = c[prop];
    }
    return user;
  }

  editUser() {
    this.usuarioService.update(this.formularioEdit.value)
    .subscribe( data => {
      window.location.reload();
    });
  }

  close() {
    this.display3 = false;
 }

}
